.camera-container{
    position: relative;
    width: 100vw;
    height: 100vh;
}

.camera-container video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100vh - 100px);
    object-fit: fill;
}

.camera-container canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: calc(100vh - 100px);
}

.camera-container img {
    position: absolute;
    width: 100%;
    height: calc(100vh - 100px);
}

.capture-controls{
    display: block;
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    top: calc(100vh - 100px);
    background-color: black;
}

.capture-btn{
    margin: 0 auto;
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.capture-btn:active{
    width: 46px;
    height: 46px;
    outline: none;
}

.capture-btn-container{
    position: absolute;
    width: 50px;
    height: 50px;
    padding: 8px;
    text-align: center;
    top: calc(50% - 30px);
    left: calc(50% - 30px);
    border: 4px solid whitesmoke;
    border-radius: 50%;
}

.retake-btn{
    position: static;
    height: 50px;
    background: none;
    color: white;
    border-radius: 10px;
}

.retake-btn-container{
    position: absolute;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: space-evenly;
}

.go-back-btn{
    width: 35px !important;
    height: 35px !important;
    top: calc(50% - 20px) !important;
    right: 25px !important;
    background-color: white;
    border-radius: 50%;
}

.switch-camera-btn{
    width: 35px !important;
    height: 35px !important;
    top: calc(50% - 20px) !important;
    left: 25px !important;
    background-color: white;
    border-radius: 50%;
}

.loader {
    border: 6px solid #f3f3f3;
    border-radius: 50%;
    border-top: 6px solid #3498db;
    width: 40px;
    height: 40px;
    position: absolute;
    top: calc(50% - 80px);
    left: calc(50% - 20px);
    -webkit-animation: spin 2s linear infinite; /* Safari */
    animation: spin 2s linear infinite;
  }
  
  /* Safari */
  @-webkit-keyframes spin {
    0% { -webkit-transform: rotate(0deg); }
    100% { -webkit-transform: rotate(360deg); }
  }
  
  @keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
  }